body {
  text-align: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #3399ff;
  z-index: 1;
  color: blanchedalmond;
  text-shadow: 2px 2px darkcyan;

}


h1 {
    font-size: 50px;
    animation-name: title;
    animation-duration: 7s;
    text-shadow: 5px 5px darkcyan;
}

input {
  border-radius: 4px;
  background-color: whitesmoke;
}
button {
  background-color:darkcyan;
  text-align: center;
  font-family: Helvetica;
  color: mintcream;
  transition-property: background-color;
  transition-duration: 2s;
  transition-property: box-shadow;
  transition-duration: .5s;
  border-radius: 4px;
}

button:hover {
  box-shadow: 5px 5px blue;
  background-color: whitesmoke;
  color: darkslategray
}
  
.homeButton {
  display:inline-flex;
  float: top;
  background:none;
  margin-top: 50px;
  margin-bottom: -20px;
  box-shadow: 5px 5px blue;
  background-color: whitesmoke;
  color: darkslategray
}

a {
  text-decoration:none;
}
.notes {
  width: 200px;
  height: 20px;
  padding-bottom: 150px;
}

.firstBlock {
  display: grid;
  justify-content: space-evenly;
  margin: 50px;
  margin-top: -10px;
}

.secondBlock {
  margin: 50px;
  
  margin-top: -10px;
}

table {
  display: grid;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  grid-template-columns: 1fr 1fr 1fr;  
}

.contentsOfTable{
  grid-column-start: 2;
  table-layout: auto;
  margin-left: auto;
  margin-right: auto;
}
td {
  display: grid;
  grid-template-rows:auto;

}

.ripple-background {
  z-index: -1;
  position: relative;
}



.circle{
  position: absolute;
  border-radius: 50%;
  background: white;
  animation: ripple 15s infinite;
  box-shadow: 0px 0px 1px 0px #508fb9;
}

.small{
  width: 200px;
  height: 200px;
  left: -100px;
  bottom: -100px;
}

.medium{
  width: 400px;
  height: 400px;
  left: -200px;
  bottom: -200px;
}

.large{
  width: 600px;
  height: 600px;
  left: -300px;
  bottom: -300px;
}

.xlarge{
  width: 800px;
  height: 800px;
  left: -400px;
  bottom: -400px;
}

.xxlarge{
  width: 1000px;
  height: 1000px;
  left: -500px;
  bottom: -500px;
}

.shade1{
  opacity: 0.2;
}
.shade2{
  opacity: 0.5;
}

.shade3{
  opacity: 0.7;
}

.shade4{
  opacity: 0.8;
}

.shade5{
  opacity: 0.9;
}

@keyframes ripple{
  0%{
    transform: scale(0.8);
  }
  
  50%{
    transform: scale(1.2);
  }
  
  100%{
    transform: scale(0.8);
  }
}

